@media (max-width: 900px){
    .JoinTheInitiativeBanner{
        display: none;
    }

    .JoinTheInitiativeBannerMobile{
        display: inline;
    }
}

@media (min-width: 900px){
    .JoinTheInitiativeBanner{
        display: inline;
    }

    .JoinTheInitiativeBannerMobile{
        display: none;
    }
}

.JoinTheInitiativeBanner {
    width: 100%;
}

.JoinTheInitiativeBannerMobile{
    width: 100%;
}

.JoinButton {
    background: #14B8A6;
    border-radius: 999px;
    width: 150px;
    height: 25px;
    padding: 1em;
    -webkit-tap-highlight-color: transparent;
    text-align: center;
    margin: auto;
    margin-top: 3em;
    margin-bottom: 3em;
}

.JoinButton:hover {
    background-color: #3B82F6;
}

.JoinButton a:visited { 
    text-decoration: none; 
}

.JoinButton a:link { 
    text-decoration: none; 
}

a.JoinButton {
    font-weight: bold;
    color: white;
}

.JoinButton a {
    font-weight: bold;
    color: white;
}