.POWtoPOS {
    width: 100%;
}

.UpperMargin {
    margin-top: 50px;
}

@media (max-width: 900px) {
    .CarbonFootprintBanner {
        display: none;
    }

    .CarbonFootprintBannerMobile {
        display: inline;
    }

    .ClimateGraphics{
        display: none;
    }

    .EthereumMergeMobile {
        display: inline;
        width: 100%;
        padding-top: 30px;
    }

    .EthereumMergeMobileGraphic {
        display: inline;
        width: 100%;
    }

    .EthereumClimateMobile {
        display: inline;
        width: 100%;
        padding-top: 60px;
    }

    .EthereumClimateMobileGraphic {
        display: inline;
        width: 100%;
    }
}

@media (min-width: 900px) {
    .CarbonFootprintBanner {
        display: inline;
    }

    .CarbonFootprintBannerMobile {
        display: none;
    }
    
    .ClimateGraphics{
        display: inline;
        padding-bottom: 75px;
    }

    .EthereumMergeMobile {
        display: none;
    }

    .EthereumMergeMobileGraphic {
        display: none;
    }

    .EthereumClimateMobile {
        display: none;
    }

    .EthereumClimateMobileGraphic {
        display: none;
    }
}

.CarbonFootprintBanner {
    width: 100%;
}

.CarbonFootprintBannerMobile {
    width: 100%;
}

.ClimateGraphics {
    padding-top: 100px;
    width: 100%;
}